import {Color} from './color';

export class Shade {
    constructor(color, options) {

        this.fill = color;
        
        if (Color.isLight(this.fill)) {
            this.primary = Color.light.primary;
            this.secondary = Color.light.secondary;
            this.disabled = Color.light.disabled;
            this.divider = Color.light.divider;
        } else {
            this.primary = Color.dark.primary;
            this.secondary = Color.dark.secondary;
            this.disabled = Color.dark.disabled;
            this.divider = Color.dark.divider;
        }
    }
}